@import url('https://fonts.googleapis.com/css2?family=Antic+Slab&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: #F473B9;
  text-align: center;
  font-family: 'Antic Slab', serif;
  color: #fff;
  letter-spacing: 1.3px;

}
.App{
  width: 100%;
  height: 100vh;
}
.header{
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
}
.header img{
  width: 50px;
  height: 50px;
  margin-left: 20px;
}
.container{
  width: 80%;
  margin: 20px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  
}
.image{
  background-color: #0E3EDA;
  
 
}
p{
  background-color: #0E3EDA;
 
  padding: 20px 40px;
}
img{
  width: 200px;
  height:200px;
  border-radius: 20px;

}
.card{
  padding: 20px;
  background-color: #0E3EDA;
  margin: 20px;
  width: 300px;
  height: 300px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  border-radius: 20px;
  cursor: pointer;
  transition: .3s;
}
.card:hover{
  transform: scale(1.1);
  opacity: 0.85;
}
button{
  margin-top: 30px;
  margin-bottom: 30px;
  font-family: 'Antic Slab', serif;
  padding: 20px 40px;
  color: #fff;
  letter-spacing: 1.3px;
  font-size: 20px;
  cursor: pointer;
  border: none;
  background-color: #0E3EDA;
  border-radius: 20px;
  transition: .3s;
}
button:hover{
  background-color: #fff;
  color: #0E3EDA;
}